<template>
  <div>
    <ul class="list-unstyled menu-v no-left-padding">
      <li v-for="item in menuItems" :key="item.id">
        <a href="#" v-on:click="selectItem(item)">{{ item.name }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import Headers from '@/components/Headers'
import Config from '@/components/Config'
import About from '@/components/About.vue'
import Overrides from '@/components/Overrides'

export default {
  name: 'left-menu',
  data () {
    return {
      menuItems: [
        { id: 1, name: 'About', About, active: true },
        { id: 2, name: 'Config', Config, active: false },
        { id: 3, name: 'Headers', Headers, active: false },
        { id: 4, name: 'Overrides', Overrides, active: false }
      ]
    }
  },
  methods: {
    selectItem: function (item) {
      this.$emit('select-component', item.name)
      this.menuItems.filter((v) => v.active).forEach((element) => { element.active = false })
      this.menuItems.filter((v) => (v.name === item.name)).forEach((element) => { element.active = true })
    }
  }
}
</script>

<style>
.no-left-padding {
  padding-left: 0;
}
.menu-v {
  padding-right: 20px;
}
.menu-v li {
  padding: 16px 10px;
  text-align: right;
}
.menu-v li a:hover {
  color: #0f6674;
}
</style>



